<script>
import MixinMapMarker from '../../mixins/MapMarker.js';

export default {
  name: 'mapbox-marker',
  render: () => null,
  mixins: [MixinMapMarker],

  inject: ['getMap', 'mapboxgl'],

  data() {
    return {
      marker: null,
    };
  },

  watch: {
    coordinates: {
      deep: true,
      handler(newValue) {
        this.marker.setLngLat(newValue);
      },
    },

    icon() {
      this.buildMarker();
    },
  },

  methods: {
    initialize() {
      this.buildMarker();
    },

    buildMarker() {
      if (this.marker) {
        this.marker.remove();
      }

      let markerElement = null;
      if (this.icon) {
        markerElement = document.createElement('img');
        markerElement.src = this.icon;
      }

      this.marker = new this.mapboxgl.Marker(markerElement)
        .setLngLat(this.coordinates)
        .addTo(this.getMap());
    },
  },

  mounted() {
    this.$nextTick(() => this.initialize());
  },

  destroyed() {
    if (this.marker) {
      this.marker.remove();
    }
  },
};
</script>